
h1 {
  font-weight: 300;
  font-size: 1.8em;
  margin-top: 0;
}
.bg-313991{
  background: #313991
}
.content {
  color: #333;
  line-height: 1.5em;
  min-height: 100vh;
}
.padding-all{
  padding: 3%;
}
/**/
.isshow{
  display: none
}
.loadmore-top{
  width: 100%;height: 50px;text-align: center;margin-top: -50px;line-height: 50px;
}
.loadmore-bottom{
  width: 100%;height: 50px;text-align: center;margin-bottom: -50px;line-height: 50px;
  position: absolute;bottom: 0px;
}
.transroute{
  -webkit-transform:rotate(180deg);
          transform:rotate(180deg);
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.transnone{
  -webkit-transform:rotate(0deg);
          transform:rotate(0deg);
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.spanner{
  -webkit-animation: mint-spinner-rotate 0.8s infinite linear;
    animation: mint-spinner-rotate 0.8s infinite linear;
    border: 4px solid transparent;
    border-radius: 50%;
}
.span-inner{
  margin: 0 auto;
   border-top-color: rgb(204, 204, 204);
    border-left-color: rgb(204, 204, 204);
    border-bottom-color: rgb(204, 204, 204);
    height: 20px;
    width: 20px;
}
.masonry img {
    -webkit-transition: .8s all;
    transition: .8s all;
    opacity: 0.7;
}
.masonry img:hover { opacity: 0.9; box-shadow: 1px 1px 20px #333;cursor: pointer
}



