
.chat textarea{height: 3rem!important
}
section.user {
  float:right;
  text-align:right;
  margin-bottom: 10px;
}
section.user>div {
  max-width: 225px;
  position:relative;
  display: inline-block;
  padding:8px;
  margin:0 10px 0 12px;
  text-align:left;
  border-radius:10px;
  color:#fff;
  /*box-shadow: inset 0 0 1px #007aff;*/
  background-color:#007aff;
  vertical-align: top;
  word-break: break-all;
  background: -webkit-linear-gradient(#FFA582,#E58C7C,#FF6157);
  background: -webkit-linear-gradient(325deg,#FFA582,#E58C7C,#FF6157);
  background: linear-gradient(125deg,#FFA582,#E58C7C,#FF6157);
  box-shadow:2px 2px 6px #FF6157;
}
section.service {
  float:left;
  text-align:left;
  margin-bottom: 10px;
}
section.service>div {
  max-width: 225px;
  position:relative;
  display: inline-block;
  padding:8px;
  margin:0 10px 0 12px;
  text-align:left;
  border-radius:10px;
  color:#000;
  /*box-shadow: inset 0 0 1px #F6F6F6;*/
  border: 1px solid #ddd;
  box-shadow:2px 2px 9px #eee;
  background-color:#F6F6F6;
  vertical-align: top;
  word-break: break-all;
}
.msg-system{
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    text-align: center;
    margin: 1rem 0;
    color: #999
}



