
body {
  font-family: Helvetica, sans-serif;
  -webkit-overflow-scrolling: touch;
}
a{text-decoration: blink;
}
.slide-fade-enter-active {
  -webkit-animation: bounce-in 0.15s ease;
          animation: bounce-in 0.15s ease;
}
.slide-fade-leave-active {
  -webkit-animation: bounce-out 0.15s ease;
          animation: bounce-out 0.15s ease;
}
@-webkit-keyframes bounce-in {
0% {
    -webkit-transform: translateY(800px);
            transform: translateY(800px);
}
100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
}
}
@keyframes bounce-in {
0% {
    -webkit-transform: translateY(800px);
            transform: translateY(800px);
}
100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
}
}
@-webkit-keyframes bounce-out {
0% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
}
100% {
    -webkit-transform: translateY(-800px);
            transform: translateY(-800px);
}
}
@keyframes bounce-out {
0% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
}
100% {
    -webkit-transform: translateY(-800px);
            transform: translateY(-800px);
}
}
@-webkit-keyframes pounce-in {
0% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
}
100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
}
}
@keyframes pounce-in {
0% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
}
100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
}
}
@-webkit-keyframes pounce-out {
0% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
}
100% {
    -webkit-transform: translateY(800px);
            transform: translateY(800px);
}
}
@keyframes pounce-out {
0% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
}
100% {
    -webkit-transform: translateY(800px);
            transform: translateY(800px);
}
}
.slide-left-enter-active {
  -webkit-animation: pounce-in 0.15s ease;
          animation: pounce-in 0.15s ease;
}
.slide-left-leave-active {
  -webkit-animation: pounce-out 0.15s ease;
          animation: pounce-out 0.15s ease;
}
.slide-left-enter, .slide-left-leave-active {
  opacity: 1;
}




